<template>
  <div v-if="api.radio_language == 'pl'" class="internetradioeng container h-100">
    <h1>Internet Radio</h1>
    <div class="row stations-container justify-content-around">
      <div v-for="station in this.stations">
        <img class="hover-scale d-block" :src="station.cover" height="150"
          @click="startPlaying(station.link, station.name)">
        <small>{{ station.name }}</small>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'InternetRadio',
    data() {
      return {
        stations: {
          "0": {
            "cover": require('../assets/default-radio-cover.png'),
            "name": "RMFFMCLUB",
            "link": "http://80.48.65.99/rmf_club",
          },
          "1": {
            "cover": require('../assets/default-radio-cover.png'),
            "name": "RMFFMMAXXX",
            "link": "http://80.48.65.99/rmf_maxxx",
          },
        }
      }
    },
    methods: {
      async startPlaying(file_name, radio_text) {
        this.stopPlaying();
        try {
          await this.api.startPlayingInternet(file_name, radio_text);
          this.api.status = await this.api.getStatus();
          $('.media-progress-bar').stop(true).css(
            'width', '0%'
          );
        } catch (e) {
          this.api.processException(e);
        }
      },

      async stopPlaying() {
        try {
          await this.api.stopPlaying();
          window.clearInterval(this.timer);
          $('.media-progress-bar').stop(true).css(
            'width', '0%'
          );
          this.api.status = await this.api.getStatus();
        } catch (e) {
          this.api.processException(e);
        }
      },
    },
  };

</script>

<style scoped lang="scss">
  .stations-container {
    width: 100%;
    max-height: 80%;
    overflow-y: auto;
    padding: 20px;
  }

</style>
